"use client"

import { useState } from "react"
import { Button } from "@/components/ui/button"
import { Textarea } from "@/components/ui/textarea"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"

export default function Home() {
  const [prompt, setPrompt] = useState("")
  const [suggestion, setSuggestion] = useState("")
  const [isLoading, setIsLoading] = useState(false)
  const [error, setError] = useState("")

  const generateSuggestions = async () => {
    if (!prompt.trim()) {
      setError("Please enter a prompt first")
      return
    }

    setIsLoading(true)
    setError("")

    try {
      // Simulate API call with timeout
      await new Promise((resolve) => setTimeout(resolve, 1000))
      
      // Advanced prompt analysis
      const promptLower = prompt.toLowerCase();
      let suggestions = "";
      
      // Extract key topics and intent
      const topics = {
        mealPlanning: promptLower.includes("meal plan"),
        healthyEating: promptLower.includes("healthy eating") || promptLower.includes("nutrition"),
        howTo: promptLower.includes("how to") || promptLower.includes("step by step"),
        introduction: promptLower.includes("introduction") || promptLower.includes("intro"),
        structure: promptLower.includes("structure") || promptLower.includes("outline"),
        guide: promptLower.includes("guide")
      };

      // Generate context-specific suggestions
      if (topics.mealPlanning) {
        suggestions = `Here's a detailed guide for writing about meal planning:\n\n` +
          "1. Hook: Start with a relatable pain point (e.g., 'Tired of the daily 'what's for dinner?' stress?')\n" +
          "2. Statistics: Include a compelling fact (e.g., 'The average family wastes 30% of their food budget')\n" +
          "3. Benefits Overview:\n" +
          "   - Time savings\n" +
          "   - Cost reduction\n" +
          "   - Healthier eating habits\n" +
          "4. Structure your guide:\n" +
          "   - Assessment of current habits\n" +
          "   - Weekly planning process\n" +
          "   - Shopping strategies\n" +
          "   - Preparation tips\n" +
          "5. Add practical examples:\n" +
          "   - Sample meal plan template\n" +
          "   - Shopping list organization\n" +
          "   - Time-saving prep techniques\n" +
          "6. Address common challenges:\n" +
          "   - Busy schedules\n" +
          "   - Dietary restrictions\n" +
          "   - Budget constraints\n" +
          "7. End with an actionable first step";
      } else if (topics.howTo && topics.structure) {
        suggestions = `Here's how to structure your how-to guide:\n\n` +
          "1. Introduction:\n" +
          "   - Identify the problem your guide solves\n" +
          "   - State the end goal clearly\n" +
          "   - List required materials/prerequisites\n" +
          "2. Main sections:\n" +
          "   - Break down into clear steps\n" +
          "   - Use numbered lists for sequence\n" +
          "   - Add subheadings for each major phase\n" +
          "3. Visual elements:\n" +
          "   - Consider adding diagrams\n" +
          "   - Include progress checkpoints\n" +
          "   - Use bullet points for quick tips\n" +
          "4. Troubleshooting:\n" +
          "   - Address common mistakes\n" +
          "   - Provide solutions to problems\n" +
          "   - Include expert tips\n" +
          "5. Conclusion:\n" +
          "   - Recap key points\n" +
          "   - Show the finished result\n" +
          "   - Suggest next steps";
      } else if (topics.healthyEating) {
        suggestions = `Here's how to write about healthy eating:\n\n` +
          "1. Start with a compelling health statistic\n" +
          "2. Address common nutrition myths\n" +
          "3. Share evidence-based benefits\n" +
          "4. Include expert opinions or studies\n" +
          "5. Provide practical implementation tips\n" +
          "6. Add meal planning suggestions\n" +
          "7. Include shopping and prep guides\n" +
          "8. Address common obstacles\n" +
          "9. Share success stories or case studies\n" +
          "10. End with actionable steps";
      } else {
        suggestions = `Here are writing suggestions to help you get started:\n\n` +
          "1. Define your main topic and goal\n" +
          "2. Identify your target audience\n" +
          "3. Create a clear outline\n" +
          "4. Research key points and statistics\n" +
          "5. Draft your introduction\n" +
          "6. Organize main sections\n" +
          "7. Add examples and evidence\n" +
          "8. Include practical tips\n" +
          "9. Write a strong conclusion\n" +
          "10. Review and refine";
      }

      setSuggestion(suggestions)
    } catch (err) {
      setError("Failed to generate suggestions. Please try again.")
    } finally {
      setIsLoading(false)
    }
  }

  return (
    <main className="min-h-screen bg-gradient-to-b from-gray-50 to-white">
      {/* Hero Section */}
      <div className="relative h-[300px] w-full overflow-hidden bg-gradient-to-r from-gray-900 to-gray-600">
        <div className="absolute inset-0 flex items-center justify-center">
          <div className="text-center text-white">
            <h1 className="text-4xl font-bold mb-4">Writing Assistant</h1>
            <p className="text-lg max-w-2xl mx-auto px-4">
              Get creative suggestions and writing assistance to improve your content
            </p>
          </div>
        </div>
      </div>

      {/* Main Content */}
      <div className="max-w-4xl mx-auto px-4 py-8">
        <Card className="mb-8">
          <CardHeader>
            <CardTitle>Enter Your Writing Prompt</CardTitle>
          </CardHeader>
          <CardContent>
            <Textarea
              placeholder="Type your prompt here..."
              className="min-h-[150px] mb-4"
              value={prompt}
              onChange={(e) => setPrompt(e.target.value)}
            />
            <Button
              onClick={generateSuggestions}
              disabled={isLoading}
              className="w-full"
            >
              {isLoading ? "Generating..." : "Generate Suggestions"}
            </Button>
            {error && (
              <p className="mt-2 text-sm text-red-500">{error}</p>
            )}
          </CardContent>
        </Card>

        {suggestion && (
          <Card>
            <CardHeader>
              <CardTitle>Suggestions</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="whitespace-pre-wrap">{suggestion}</div>
            </CardContent>
          </Card>
        )}
      </div>
    </main>
  )
}
